export const lengthAttr = ({ len, name, icon } = {}) => ({
  name: `Hasta ${len} metros de eslora`,
  description: `${
    name ? `La titulacin de ${name} permite` : "Permite"
  } gobernar embarcaciones de una eslora mxima de ${len} metros.`,

  icon: icon ? icon : null,
});

export const dayNight = ({ both = true, name, night = false, icon } = {}) => ({
  name: both
    ? "Navegacin diurna y noturna"
    : night
    ? "Navegacin nocturna"
    : "Navegacin diurna",
  description: both
    ? `${
        name ? `La titulacin de ${name} permite` : "Permite"
      } la navegacin diurna y nocturna.`
    : night
    ? `${
        name ? `La titulacin de ${name} permite` : "Permite"
      } la navegacin nocturna`
    : `${
        name ? `La titulacin de ${name} permite` : "Permite"
      } la navegacin diurna`,

  icon: icon ? icon : null,
});

